import React, { Component } from 'react'
import Note from './../components/Notes/Note';

export default class notes extends Component {
  render() {
    return (
        <div className="row">
            <div className="col-12 grid-margin">
                <div className="card">
                    
                    <div className="card-body" id="chat-body" style={{ background : '#f2f8f9',padding: '15px'}}>
                    <Note date = "12 July 2018" value="Hello this is my first note, and I am just testing this note app."/>
                    <Note date = "12 July 2018" value="Hello this is my first note, and I am just testing this note app."/>
                    </div>
                    
                    <div className="card-body" id="chat-controls" style={{ padding : '5px', position : 'fixed', bottom : '0', background : '#f3f8f7' }}>
                        <form>
                        <div className="input-group mb-3" >
                            <input type="text" className="form-control" style ={{ padding : '10px',paddingLeft : '15px', fontSize : '14px', borderTopLeftRadius : '20px',borderBottomLeftRadius : '20px',  height : '50px' }}  placeholder="Type Note..." />
                            <div className="input-group-append">
                                <button className="btn btn-success" style={{ padding : '10px' }} type="submit">SAVE</button> 
                            </div>
                        </div>
                        </form>
                    </div>
                    
                    
                </div>
            </div>
        </div>
    )
  }
}
